// This file was copied from the Grafana piechart plugin located here: https://github.com/grafana/piechart-panel
.flow-panel {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.flow-panel--legend-right {
  flex-direction: row;
}

.flow-panel--legend-right .flow-legend {
  flex: 0 1 10px;
  height: 100%;
}

_:-ms-fullscreen, :root .flow-panel--legend-right .flow-legend {
  flex: 0 1 200px;
}

.flow-panel--legend-right .flow-legend-series {
  display: block;
  padding-left: 0px;
}

.flow-panel--legend-right .flow-legend-table .flow-legend-series {
  display: table-row;
}

.flow-legend__container {
  height: 100%;
}

.flow-panel__chart {
  position: relative;
  cursor: crosshair;
  flex-grow: 1;
  top: 10px;
}

.datapoints-warning {
  pointer: none;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 10;
  margin-top: -50px;
  margin-left: -100px;
  width: 200px;
  text-align: center;
  cursor: auto;
  padding: 10px;
}

.flow-legend {
  flex: 0 1 auto;
  margin: 0 1rem;
  text-align: center;
  padding-top: 6px;
  position: relative;
}

.flow-legend .popover-content {
  padding: 0;
}

.flow-legend-icon {
  position: relative;
  padding-right: 4px;
  top: 1px;
}

.flow-legend-icon,
.flow-legend-alias,
.flow-legend-value {
  cursor: pointer;
  float: left;
  white-space: nowrap;
  font-size: 85%;
  text-align: left;
  color: 52545c;
}

.flow-legend-icon.current::before {
  content: "Current: ";
}

.flow-legend-icon.max::before {
  content: "Max: ";
}

.flow-legend-icon.min::before {
  content: "Min: ";
}

.flow-legend-icon.total::before {
  content: "Total: ";
}

.flow-legend-icon.avg::before {
  content: "Avg: ";
}

.flow-legend-alias.current::before {
  content: "Current: ";
}

.flow-legend-alias.max::before {
  content: "Max: ";
}

.flow-legend-alias.min::before {
  content: "Min: ";
}

.flow-legend-alias.total::before {
  content: "Total: ";
}

.flow-legend-alias.avg::before {
  content: "Avg: ";
}

.flow-legend-value.current::before {
  content: "Current: ";
}

.flow-legend-value.max::before {
  content: "Max: ";
}

.flow-legend-value.min::before {
  content: "Min: ";
}

.flow-legend-value.total::before {
  content: "Total: ";
}

.flow-legend-value.avg::before {
  content: "Avg: ";
}

.flow-legend-icon .fa {
  font-size: 135%;
  position: relative;
  top: 1px;
}

.flow-legend-series {
  float: left;
  white-space: nowrap;
  padding-left: 10px;
}

.flow-legend-series--right-y {
  float: right;
}

.flow-legend-value {
  padding-left: 6px;
}

.flow-legend-table tbody {
  display: block;
  overflow-y: auto;
  overflow-x: hidden;
  height: 100%;
  padding-bottom: 1px;
  padding-right: 5px;
}

.flow-legend-table .flow-legend-series {
  display: table-row;
  float: none;
  padding-left: 0;
}

.flow-legend-table .flow-legend-series--right-y {
  float: none;
}

.flow-legend-table .flow-legend-series--right-y .flow-legend-alias::after {
  content: "(right-y)";
  padding: 0 5px;
  color: #767980;
}

.flow-legend-table td,
.flow-legend-alias,
.flow-legend-icon,
.flow-legend-value {
  float: none;
  display: table-cell;
  white-space: nowrap;
  padding: 2px;
  text-align: right;
}

.flow-legend-table .flow-legend-icon {
  width: 5px;
  padding: 0;
  top: 0;
}

.flow-legend-table .flow-legend-icon .fa {
  top: 4px;
}

.flow-legend-table .flow-legend-value {
  padding-left: 15px;
}

.flow-legend-table .flow-legend-alias {
  padding-left: 7px;
  text-align: left;
  width: 95%;
  max-width: 650px;
  text-overflow: ellipsis;
  overflow: hidden;
}

.flow-legend-table .flow-legend-series:nth-child(odd) {
  background: #dde4ed;
}

.flow-legend-table .flow-legend-value.current::before {
  content: "";
}

.flow-legend-table .flow-legend-value.max::before {
  content: "";
}

.flow-legend-table .flow-legend-value.min::before {
  content: "";
}

.flow-legend-table .flow-legend-value.total::before {
  content: "";
}

.flow-legend-table .flow-legend-value.avg::before {
  content: "";
}

.flow-legend-table th {
  text-align: right;
  padding: 0px 10px 1px 0;
  font-weight: bold;
  color: #61c2f2;
  font-size: 85%;
  white-space: nowrap;
}

.flow-legend-series-hidden .flow-legend-value,
.flow-legend-series-hidden .flow-legend-alias {
  color: #acb6bf;
}

.flow-tooltip {
  white-space: nowrap;
  font-size: 12px;
  background-color: #dde4ed;
  color: #35373f;
}

.flow-tooltip .flow-tooltip-time {
  text-align: center;
  position: relative;
  top: -3px;
  padding: 0.2rem;
  font-weight: bold;
  color: #35373f;
}

.flow-tooltip .flow-tooltip-value {
  display: table-cell;
  font-weight: bold;
  padding-left: 15px;
  text-align: right;
}
